import React, { useState } from 'react';
import { Box, Button, Heading, Image, Modal, ModalContent, ModalOverlay } from '@chakra-ui/react';
import { resource } from 'configs/resource';

export const NftItem = (props: { url: string; onClick?: () => void }) => {
  return (
    <Button
      onClick={props.onClick}
      boxSize={24}
      border="1px"
      colorScheme="whiteAlpha"
      backgroundColor="transparent"
      padding={0}
      mr={{ base: 2, md: 8 }}
    >
      <Image rounded={8} src={props.url} _hover={{ opacity: 0.8 }} />
    </Button>
  );
};

export const NftCollection: React.FC = () => {
  const [open, setOpen] = useState<string | null>(null);

  return (
    <Box padding={{ base: 2, md: 8 }}>
      <Heading fontSize={{ base: 'lg', md: '2xl' }} color="white">
        anonclub nft
      </Heading>

      <Box mt={{ base: 2, md: 4 }} mb={{ base: 4, md: 12 }}>
        <NftItem url={resource('/props/clubnft-1.jpg')} onClick={() => setOpen(resource('/props/clubnft-1.jpg'))} />
        <NftItem url={resource('/props/clubnft-2.jpg')} onClick={() => setOpen(resource('/props/clubnft-2.jpg'))} />
      </Box>

      <Heading fontSize="lg" color="white">
        v-medal
      </Heading>
      <Box mt={{ base: 2, md: 4 }} mb={{ base: 4, md: 12 }}>
        <NftItem url={resource('/props/medal.png')} />
      </Box>

      <Heading fontSize="lg" color="white">
        v-masks
      </Heading>
      <Box mt={{ base: 2, md: 4 }} mb={{ base: 4, md: 12 }}>
        <NftItem url={resource('/props/mask-1.png')} />
        <NftItem url={resource('/props/mask-2.png')} />
        <NftItem url={resource('/props/mask-3.png')} />
        <NftItem url={resource('/props/mask-4.png')} />
      </Box>

      <Modal isOpen={!!open} onClose={() => setOpen(null)}>
        <ModalOverlay />
        <ModalContent>{open && <Image src={open} />}</ModalContent>
      </Modal>
    </Box>
  );
};
