import React, { useState } from 'react';
import { Box, Flex, Image, List } from '@chakra-ui/react';
import { Cross as Hamburger } from 'hamburger-react';
import { resource } from '../../configs';
import { useNavigate } from 'react-router-dom';

const MenuLayer: React.FC = () => {
  return (
    <Box zIndex="dropdown" position="absolute" w="100%" height="calc(100vh)" bgColor="blackAlpha.700">
      <List></List>
    </Box>
  );
};

export const Header: React.FC = () => {
  const [toggled, setToggled] = useState(false);
  const navigate = useNavigate();

  return (
    <Box as="header">
      <Flex
        zIndex="sticky"
        height={[20]}
        position="absolute"
        w="100%"
        justify="space-between"
        alignItems="center"
        paddingX={[2, 8]}
        paddingY={2}
      >
        <Image cursor="pointer" maxH="100%" src={resource('/logo.svg')} alt="logo" onClick={() => navigate('/')} />
        <div>
          <Hamburger color="red" onToggle={setToggled} />
        </div>
      </Flex>
      {toggled && <MenuLayer />}
    </Box>
  );
};
